html, body, div, span, h1, h2, h3, h4, h5, h6, p, img,
a, ul, ol, li, sub, sup, pre, code, iframe, form, input, button, textarea,
select, option, legend, table, thead, tbody, tfoot, tr,
th, td, caption, section, main, footer, header, nav, aside,
article, blockquote, details, hgroup, figcaption, figure,
video, audio, picture {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

article, aside, details, figcaption, figure,
footer, header, main, hgroup, menu, nav, section {
  display: block;
}

html, body {
  font-size: 100%;
  size-adjust: 100%;
  height: 100%;
}

a, a:hover, a:visited {
  text-decoration: none;
}

ol, ul {
  list-style: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  margin: 0;
}

img {
  border: 0;
  vertical-align: top;
}

/*
---- Media query mixin
*/
:root {
  --font-primary: "Raleway", sans-serif;
  --bgc-primary: #303030;
  --bgc-second: #3F3E3E;
  --bgc-third: #FFFFFF;
  --bgc-accent: #6ECCAF;
  --fc-primary: #FFFFFF;
  --fc-second: #303030;
  --fc-accent: #6ECCAF;
}

body {
  background-color: var(--bgc-primary);
  font-family: var(--font-primary);
  color: var(--fc-primary);
  font-weight: 500;
  font-size: 1rem;
}
body.lock {
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
}

[class*=__container] {
  max-width: 76.25rem;
  margin: 0 auto;
  padding: 0 0.625rem;
}

#app {
  display: grid;
  grid-template-rows: auto 1fr auto;
  height: 100%;
}

.header {
  position: absolute;
  width: 100%;
  height: 5.9375rem;
  padding: 0.9375rem 0;
  left: 0;
  top: 0;
  z-index: 15;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 61.99875rem) {
  .header__nav {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    transform: translate(0, -100%);
    background-color: var(--bgc-primary);
    transition: transform 0.4s ease-in-out 0s;
  }
  .header__nav_active {
    transform: translate(0, 0);
  }
}
.header__nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 62rem) {
  .header__nav-list {
    flex-direction: row;
    gap: 0 3.125rem;
  }
}
@media (max-width: 61.99875rem) {
  .header__nav-list {
    padding: 3.125rem 0;
  }
}
@media (min-width: 62rem) {
  .header__nav-list__item {
    position: relative;
  }
}
.header__nav-list__link {
  display: inline-block;
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--fc-primary);
  transition: color 0.2s ease-in-out 0s;
}
@media (max-width: 61.99875rem) {
  .header__nav-list__link {
    padding: 0.625rem;
  }
}
.header__nav-list__link:hover {
  color: var(--fc-accent);
}
.header__nav-list__link:hover .header__nav-icon {
  fill: #6ECCAF;
}
.header__nav-list__link_sub {
  display: flex;
  align-items: center;
  gap: 0 0.9375rem;
}
.header__nav-list__link_active {
  color: var(--fc-accent);
}
.header__nav-icon {
  width: 0.625rem;
  height: 1rem;
  transform: rotate(90deg);
  fill: #FFFFFF;
  transition: all 0.2s ease-in-out 0s;
}
.header__nav-icon_active {
  fill: #6ECCAF;
  transform: rotate(-90deg);
}
.header__nav-sub {
  padding: 0.9375rem 1.25rem;
  gap: 0.9375rem 0;
}
@media (min-width: 62rem) {
  .header__nav-sub {
    display: grid;
    position: absolute;
    width: 100%;
    left: 0;
    top: calc(100% + 1.25rem);
    border-radius: 0.125rem;
    transform: translate(0, 2.5rem);
    visibility: hidden;
    opacity: 0;
    transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, visibility 0.3s ease 0s;
    background-color: var(--bgc-accent);
  }
}
@media (max-width: 61.99875rem) {
  .header__nav-sub {
    display: none;
    border-right: 0.1875rem solid var(--bgc-accent);
    text-align: end;
  }
}
@media (min-width: 62rem) {
  .header__nav-sub_active {
    transform: translate(0, 0);
    opacity: 1;
    visibility: visible;
  }
}
@media (max-width: 61.99875rem) {
  .header__nav-sub_active {
    display: grid;
  }
}
.header__nav-sub__link {
  font-size: 1.125rem;
  font-weight: 37.5rem;
  color: var(--fc-primary);
}
.header__nav-sub__link:hover {
  text-decoration: underline;
}

.logo {
  display: flex;
  align-items: center;
  max-width: 18.75rem;
  gap: 0 0.625rem;
}
@media (min-width: 62rem) {
  .logo {
    gap: 0 1.25rem;
  }
}
.logo__icon {
  flex-shrink: 0;
  width: 2.5rem;
}
@media (min-width: 62rem) {
  .logo__icon {
    width: 3.125rem;
  }
}
.logo__text {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--fc-accent);
}
@media (min-width: 62rem) {
  .logo__text {
    font-size: 1.5rem;
  }
}

.burger {
  display: none;
}
@media (max-width: 61.99875rem) {
  .burger {
    position: relative;
    display: grid;
    width: 1.875rem;
    gap: 0.4375rem 0;
    background: transparent;
    border: 0;
  }
  .burger_active .burger__line:nth-child(1) {
    transform: translate(0, 0.625rem) rotate(45deg);
  }
  .burger_active .burger__line:nth-child(3) {
    transform: translate(0, -0.625rem) rotate(-45deg);
  }
  .burger_active .burger__line:nth-child(2) {
    opacity: 0;
  }
}
@media (max-width: 61.99875rem) {
  .burger__line {
    height: 0.1875rem;
    background-color: var(--bgc-third);
    border-radius: 0.125rem;
    transition: transform 0.4s ease-in-out 0s, opacity 0.4s ease-in-out 0s;
  }
}

.footer {
  padding: 1.875rem 0 6.25rem;
}
.footer__inner {
  display: flex;
  justify-content: space-between;
  padding: 1.25rem 0 0;
  gap: 3.125rem;
  border-top: 0.1875rem solid var(--bgc-accent);
}
@media (max-width: 49.99875rem) {
  .footer__inner {
    flex-wrap: wrap;
  }
}
.footer__info {
  display: flex;
  flex-direction: column;
  max-width: 19.0625rem;
  gap: 1.875rem 0;
}
.footer__text {
  font-size: 1.25rem;
}
.footer__row {
  flex: 0 1 36.875rem;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 2.5rem;
}
@media (min-width: 28.125rem) {
  .footer__row {
    justify-content: space-between;
  }
}
.footer__col {
  display: flex;
  flex-direction: column;
  gap: 1.25rem 0;
}
@media (max-width: 29.99875rem) {
  .footer__col {
    width: 100%;
  }
}
.footer__title {
  font-size: 1.25rem;
  font-weight: 700;
}
@media (min-width: 50rem) {
  .footer__title {
    text-align: end;
  }
}
.footer__nav {
  display: grid;
  gap: 0.9375rem 0;
}
.footer__nav-sub {
  display: none;
  gap: 0.9375rem 0;
  padding: 0 0 0 0.9375rem;
  border-left: 0.1875rem solid var(--bgc-accent);
}
@media (min-width: 50rem) {
  .footer__nav-sub {
    border-left: 0;
    border-right: 0.1875rem solid var(--bgc-accent);
    padding: 0 0.9375rem 0 0;
  }
}
.footer__nav-sub_active {
  display: grid;
}
@media (min-width: 50rem) {
  .footer__nav-sub__item {
    text-align: end;
  }
}
.footer__nav-sub__link {
  font-size: 1.125rem;
  color: var(--fc-primary);
  transition: color 0.2s ease-in-out 0s;
}
.footer__nav-sub__link:hover {
  color: var(--fc-accent);
}
.footer__item {
  display: grid;
  gap: 0.9375rem 0;
}
@media (min-width: 50rem) {
  .footer__item {
    text-align: end;
  }
}
.footer__link {
  font-size: 1.25rem;
  color: var(--fc-primary);
  transition: color 0.2s ease-in-out 0s;
}
.footer__link:hover {
  color: var(--fc-accent);
}
.footer__link:hover .footer__icon {
  fill: #6ECCAF;
}
.footer__link_sub {
  display: flex;
  align-items: center;
  gap: 0 0.625rem;
}
.footer__link_active {
  color: var(--fc-accent);
}
.footer__link_dima {
  opacity: 0.01;
  transition: opacity 0.2s ease-in-out 0s;
}
.footer__link_dima:hover {
  opacity: 0.5;
}
.footer__icon {
  width: 0.5rem;
  height: 0.8125rem;
  transform: rotate(90deg);
  fill: #FFFFFF;
  transition: all 0.2s ease-in-out 0s;
}
.footer__icon_active {
  fill: #6ECCAF;
  transform: rotate(-90deg);
}

.accent {
  color: var(--fc-accent);
}

