  width: 7.5rem;
  height: 7.5rem;
}
.target__container::before {
  background-image: url("../img/icons/rect-1.svg");
  left: 0;
  top: -6.25rem;
}
.target__container::after {
  background-image: url("../img/icons/rect-2.svg");
