  display: flex;
  align-items: center;
  justify-content: center;
}
.test__input:checked + .test__radio::after {
  content: "";
  display: block;
  width: 0.6875rem;
  height: 0.6875rem;
  border-radius: 50%;
  background-color: var(--bgc-third);
}
.test__input:focus + .test__radio {
  box-shadow: 0 0 0.125rem 0.125rem var(--bgc-accent);
}
.test__radio {
  width: 1.5625rem;
  height: 1.5625rem;
  border: 0.125rem solid var(--bgc-third);
  border-radius: 50%;
}
.test__answer {
  font-size: 1.25rem;
}
.test__navigation {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: 1fr 1fr;
}

.result {
  display: grid;
  gap: 1.875rem;
}
.result__content {
  display: grid;
  gap: 1.25rem;
  padding: 1.25rem;
  background-color: var(--bgc-second);
  border-radius: 0.125rem;
}
.result__title {
  display: flex;
  align-items: center;
  gap: 0 0.625rem;
  font-size: 1.75rem;
}
.result__icon {
  width: 2rem;
  height: 2rem;
}
.result__text {
  font-size: 1.5rem;
}
.result__link {
  font-size: 1.5rem;
  color: var(--fc-accent);
  text-decoration: underline;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.25rem;
  gap: 0 1.25rem;
  border: 0;
  border-radius: 0.125rem;
  background-color: var(--bgc-accent);
  font-family: inherit;
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--fc-primary);
}
.button_unactive {
  background-color: var(--bgc-second);
  cursor: unset;
}
.button:not(.button_unactive):hover .button__icon_prev {
  transform: rotate(180deg) translate(1.25rem, 0);
}
.button:not(.button_unactive):hover .button__icon_next {
  transform: translate(1.25rem, 0);
}
.button__icon {
  width: 0.625rem;
  height: 1rem;
  fill: #FFFFFF;
  transition: transform 0.6s ease 0s;
}
.button__icon_prev {
  transform: rotate(180deg);
}

.hide {
  display: none;
}