// ===== Require library sass:math
@use 'sass:math';

// Require reset styles
@import './libs/reset';

// Require functions
@import "./libs/functions";

// Require mixins
@import "./libs/mixins";

// Require vars
@import "./libs/vars";

// Main settings
body {
	background-color: var(--bgc-primary);
	font-family: var(--font-primary);
	color: var(--fc-primary);
	font-weight: 500;
	font-size: rem(16);
	&.lock {
		overflow: hidden;
	}
}

html {

	scroll-behavior: smooth;
}

$containerWidth: 1200; // Max width
$containerPadding: 10; // Padding of container

[class*='__container'] {
	max-width: rem($containerWidth + $containerPadding * 2);
	margin: 0 auto;
	padding: 0 rem($containerPadding);
}

#app {
	display: grid;
	grid-template-rows: auto 1fr auto;
	height: 100%;
}

// ===== Styles for web-site
@import "./includes/header";
@import "./includes/footer";
@import "./includes/global";
