.footer {
	padding: rem(30) 0 rem(100);
	// .footer__inner
	&__inner {
		display: flex;
		justify-content: space-between;
		padding: rem(20) 0 0;
		gap: rem(50);
		border-top: rem(3) solid var(--bgc-accent);
		@include media('max', 'width', 799.98) {
			flex-wrap: wrap;
		}
	}
	// .footer__info
	&__info {
		display: flex;
		flex-direction: column;
		max-width: rem(305);
		gap: rem(30) 0;
	}
	// .footer__text
	&__text {
		font-size: rem(20);
	}
	// .footer__row
	&__row {
		flex: 0 1 rem(590);
		display: flex;
		justify-content: flex-start;
		flex-wrap: wrap;
		gap: rem(40);
		@include media('min', 'width', 450) {
			justify-content: space-between;
		}
	}
	// .footer__col
	&__col {
		display: flex;
		flex-direction: column;
		gap: rem(20) 0;
		@include media('max', 'width', 479.98) {
			width: 100%;
		}
	}
	// .footer__title
	&__title {
		font-size: rem(20);
		font-weight: 700;
		@include media('min', 'width', 800) {
			text-align: end;
		}
	}
	// .footer__nav
	&__nav {
		display: grid;
		gap: rem(15) 0;
		// .footer__nav-sub
		&-sub {
			display: none;
			gap: rem(15) 0;
			padding: 0 0 0 rem(15);
			border-left: rem(3) solid var(--bgc-accent);
			@include media('min', 'width', 800) {
				border-left: 0;
				border-right: rem(3) solid var(--bgc-accent);
				padding: 0 rem(15) 0 0;
			}
			&_active {
				display: grid;
			}
			// .footer__nav-sub__item
			&__item {
				@include media('min', 'width', 800) {
					text-align: end;
				}
			}
			// .footer__nav-sub__link
			&__link {
				font-size: rem(18);
				color: var(--fc-primary);
				transition: color 0.2s ease-in-out 0s;
				&:hover {
					color: var(--fc-accent);
				}
			}
		}
	}
	// .footer__item
	&__item {
		display: grid;
		gap: rem(15) 0;
		@include media('min', 'width', 800) {
			text-align: end;
		}
	}
	// .footer__link
	&__link {
		font-size: rem(20);
		color: var(--fc-primary);
		transition: color 0.2s ease-in-out 0s;
		&:hover {
			color: var(--fc-accent);
			& .footer__icon {
				fill: #6ECCAF;
			}
		}
		// .footer__link_sub
		&_sub {
			display: flex;
			align-items: center;
			gap: 0 rem(10);
		}
		// .footer__link_active
		&_active {
			color: var(--fc-accent);
		}
		&_dima {
			opacity: .01;
			transition: opacity 0.2s ease-in-out 0s;
			&:hover {
				opacity: .5;
			}
		}
	}
	// .footer__icon
	&__icon {
		width: rem(8);
		height: rem(13);
		transform: rotate(90deg);
		fill: #FFFFFF;
		transition: all 0.2s ease-in-out 0s;
		// .footer__icon_active
		&_active {
			fill: #6ECCAF;
			transform: rotate(-90deg);
		}
	}
}
