.accent {
	color: var(--fc-accent);
}


.intro {
	position: relative;
	overflow: hidden;
	@include media('min', 'width', 576) {
		background: url('../img/intro/web.svg') no-repeat center / contain;
	}
	// .intro__container
	&__container {
		position: relative;
		padding: rem(130) rem(10);
		@include media('min', 'width', 992) {
			padding: rem(245) rem(10) rem(250);
		}
	}
	// .intro__content
	&__content {
		display: grid;
		max-width: rem(675);
		gap: rem(30) 0;
		position: relative;
		z-index: 2;
	}
	// .intro__title
	&__title {
		font-size: rem(32);
		@include media('min', 'width', 360) {
			font-size: rem(36);
		}
		@include media('min', 'width', 768) {
			font-size: rem(52);
		}
		@include media('min', 'width', 992) {
			font-size: rem(64);
		}
	}
	// .intro__text
	&__text {
		font-size: rem(20);
		font-weight: 600;
		@include media('min', 'width', 768) {
			font-size: rem(32);
		}
	}
	// .intro__image
	&__image {
		position: absolute;
		height: rem(400);
		right: rem(-60);
		bottom: 0;
		z-index: 1;
		@include media('min', 'width', 768) {
			height: rem(500);
			right: rem(-40);
		}
		@include media('min', 'width', 992) {
			height: rem(600);
			right: rem(-80);
		}
		@include media('min', 'width', 1220) {
			height: unset;
			right: rem(-140);
		}
	}
}

.target {
	padding: rem(150) 0 rem(75);
	background-color: var(--bgc-third);
	// .target__container
	&__container {
		position: relative;
		display: grid;
		grid-template-columns: 1fr;
		align-items: center;
		gap: rem(25) rem(50);
		@include media('min', 'width', 992) {
			grid-template-columns: 1fr 1fr;
		}
		&::before, &::after {
			content: '';
			position: absolute;
			width: rem(120);
			height: rem(120);
		}
		&::before {
			background-image: url('../img/icons/rect-1.svg');
			left: 0;
			top: rem(-100);
		}
		&::after {
			background-image: url('../img/icons/rect-2.svg');
			top: 100%;
			right: 0;
		}
	}
	// .target__title
	&__title {
		text-align: end;
		color: var(--fc-second);
		// .target__title_big
		&_big {
			font-size: rem(60);
			@include media('min', 'width', 576) {
				font-size: rem(70);
			}
			@include media('min', 'width', 1220) {
				font-size: rem(96);
			}
		}
		// .target__title_small
		&_small {
			font-size: rem(34);
			@include media('min', 'width', 576) {
				font-size: rem(50);
			}
			@include media('min', 'width', 1220) {
				font-size: rem(64);
			}
		}
	}
	// .target__text
	&__text {
		margin: rem(20) 0 0;
		font-size: rem(24);
		font-weight: 500;
		color: var(--fc-second);
		@include media('min', 'width', 576) {
			font-size: rem(28);
		}
		@include media('min', 'width', 1220) {
			font-size: rem(32);
		}
	}
}

.people {
	padding: rem(75) 0 rem(35);
	background-color: var(--bgc-third);
	@include media('min', 'width', 992) {
		padding: rem(75) 0;
	}
	// .people__container
	&__container {
		display: grid;
		gap: rem(20);
	}
	// .people__title
	&__title {
		display: inline-block;
		font-size: rem(44);
		color: var(--fc-second);
		@include media('min', 'width', 576) {
			font-size: rem(70);
		}
		@include media('min', 'width', 1220) {
			font-size: rem(96);
		}
	}
}

.people-tabs {
	border-radius: rem(2);
	background-color: var(--bgc-primary);
	overflow: hidden;
	@include media('max', 'width', 1219.98) {
		margin: 0 rem(-10);
	}
	// .people-tabs__header
	&__header {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		border-bottom: rem(5) solid var(--bgc-accent);
		@include media('min', 'width', 992) {
			grid-template-columns: repeat(4, 1fr);
		}
	}
	// .people-tabs__button
	&__button {
		padding: rem(15) rem(10);
		border: 0;
		background-color: transparent;
		transition: background-color 0.2s ease-in-out 0s;
		font-size: rem(20);
		font-weight: 700;
		color: var(--fc-primary);
		@include media('min', 'width', 576) {
			font-size: rem(26);
			padding: rem(30) rem(20);
		}
		@include media('min', 'width', 992) {
			font-size: rem(30);
		}
		@include media('min', 'width', 1220) {
			font-size: rem(36);
		}
		&:not(.people-tabs__button_active):hover {
			background-color: var(--bgc-second);
		}
		// .people-tabs__button_active
		&_active {
			background-color: var(--bgc-accent);
		}
	}
	// .people-tabs__item
	&__item {
		display: none;
		@include media('min', 'width', 576) {
			grid-template-columns: rem(100) 1fr;
		}
		@include media('min', 'width', 768) {
			grid-template-columns: rem(200) 1fr;
		}
		@include media('min', 'width', 992) {
			grid-template-columns: rem(400) 1fr;
		}
		&_active {
			display: grid;
		}
	}
	// .people-tabs__image
	&__image {
		width: 100%;
		height: rem(500);
		object-fit: cover;
		object-position: center;
	}
	// .people-tabs__descr
	&__descr {
		padding: rem(15) rem(10);
		@include media('min', 'width', 992) {
			padding: rem(40) rem(50);
		}
	}
	// .people-tabs__title
	&__title {
		margin: 0 0 rem(30) 0;
		font-size: rem(36);
	}
	// .people-tabs__text
	&__text {
		font-size: rem(20);
	}
}

.questions {
	padding: rem(35) 0;
	@include media('min', 'width', 992) {
		padding: rem(75) 0;
	}
	background-color: var(--bgc-third);
	// .questions__container
	&__container {
		display: grid;
		gap: rem(30) 0;
		@include media('min', 'width', 768) {
			gap: rem(50) 0;
		}
	}
	// .qusetions__title
	&__title {
		font-size: rem(36);
		color: var(--fc-second);
		@include media('min', 'width', 576) {
			font-size: rem(40);
		}
		@include media('min', 'width', 992) {
			font-size: rem(48);
		}
	}
}

.questions-accordeon {
	display: grid;
	gap: rem(15) 0;
	// .questions-accordeon__item
	&__item {
		background-color: var(--bgc-primary);
		&_active {
			& .questions-accordeon__content {
				display: block;
			}
		}
	}
	&__button {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: rem(10) rem(20);
		width: 100%;
		gap: 0 rem(30);
		border: 0;
		background-color: var(--bgc-accent);
		@include media('min', 'width', 768) {
			padding: rem(20) rem(30);
		}
	}
	// .questions-accordeon__title
	&__title {
		font-size: rem(24);
		color: var(--fc-primary);
		@include media('min', 'width', 576) {
			font-size: rem(30);
		}
		@include media('min', 'width', 992) {
			font-size: rem(36);
		}
	}
	// .questions-accordeon__icon
	&__icon {
		width: rem(16);
		height: rem(24);
		transform: rotate(90deg);
		transition: transform 0.2s ease-in-out 0s;
		fill: #FFFFFF;
		@include media('min', 'width', 576) {
			width: rem(18);
			height: rem(28);
		}
		@include media('min', 'width', 992) {
			width: rem(20);
			height: rem(32);
		}
		// .questions-accordeon__icon_active
		&_active {
			transform: rotate((-90deg));
		}
	}
	// .questions-accordeon__content
	&__content {
		display: none;
		padding: rem(20);
		& ul {
			display: grid;
			gap: rem(15);
			list-style: square;
			padding: 0 rem(20);
			& li {
				&::marker {
					color: var(--bgc-accent);
				}
				font-size: rem(20);
				@include media('min', 'width', 576) {
					font-size: rem(22);
				}
				@include media('min', 'width', 992) {
					font-size: rem(24);
				}
			}
		}
	}
	// .questions-accordeon__text
	&__text {
		font-size: rem(20);
		@include media('min', 'width', 576) {
			font-size: rem(22);
		}
		@include media('min', 'width', 992) {
			font-size: rem(24);
		}
	}
}

.education {
	padding: rem(105) 0 rem(30);
	@include media('min', 'width', 992) {
		padding: rem(165) 0 rem(70);
	}
	// .education__container
	&__container {
		display: grid;
		gap: rem(50) 0;
	}
	// .education__title
	&__title {
		font-size: rem(40);
		font-weight: 700;
	}
	// .education__place
	&__place {
		display: grid;
		gap: rem(40) 0;
	}
	// .education__subtitle
	&__subtitle {
		font-size: rem(30);
		font-weight: 700;
	}
	// .education__content
	&__content {
		display: grid;
		gap: rem(30) 0;
	}
}

.education-card {
	display: grid;
	border-radius: rem(2);
	overflow: hidden;
	background-color: var(--bgc-second);
	@include media('min', 'width', 576) {
		grid-template-columns: rem(230) 1fr;
	}
	@include media('min', 'width', 768) {
		grid-template-columns: rem(300) 1fr;
	}
	@include media('min', 'width', 1024) {
		grid-template-columns: rem(460) 1fr;
	}
	// .education-card__image
	&__image {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	// .education-card__info
	&__info {
		padding: rem(20) rem(30);
	}
	// .education-card__contacts
	&__contacts {
		display: grid;
		gap: rem(15) 0;
	}
	// .education-card__title
	&__title {
		margin: 0 0 rem(20) 0;
		font-size: rem(24);
		font-weight: 600;
	}
	// .education-card__link
	&__link {
		font-size: rem(20);
		font-weight: 500;
		color: var(--fc-primary);
		&:hover {
			text-decoration: underline;
		}
	}
}

.test {
	padding: rem(105) 0 rem(30);
	@include media('min', 'width', 992) {
		padding: rem(165) 0 rem(70);
	}
	// .test__container
	&__container {
		display: grid;
		gap: rem(50);
	}
	// .test__title
	&__title {
		font-size: rem(40);
		font-weight: 700;
	}
	// .test__content
	&__content {
		display: grid;
		gap: rem(30);
	}
	// .test__question
	&__question {
		padding: rem(20);
		background-color: var(--bgc-second);
		border-radius: rem(2);
		font-size: rem(24);
	}
	// .test__options
	&__options {
		display: grid;
		gap: rem(30);
		@include media('min', 'width', 768) {
			grid-template-columns: 1fr 1fr;
		}
	}
	// .test__option
	&__option {
		display: flex;
		align-items: center;
		padding: rem(20);
		gap: rem(15);
		background-color: var(--bgc-second);
		border-radius: rem(2);
	}
	// .test__input
	&__input {
		position: absolute;
		appearance: none;
		&:checked + .test__radio {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			&::after {
				content: '';
				display: block;
				width: rem(11);
				height: rem(11);
				border-radius: 50%;
				background-color: var(--bgc-third);
			}
		}
		&:focus + .test__radio {
			box-shadow: 0 0 rem(2) rem(2) var(--bgc-accent);
		}
	}
	// .test__radio
	&__radio {
		width: rem(25);
		height: rem(25);
		border: rem(2) solid var(--bgc-third);
		border-radius: 50%;
	}
	// .test__answer
	&__answer {
		font-size: rem(20);
	}
	// .test__navigation
	&__navigation {
		display: grid;
		gap: rem(30);
		grid-template-columns: 1fr 1fr;
	}
}

.result {
	display: grid;
	gap: rem(30);
	// .result__content
	&__content {
		display: grid;
		gap: rem(20);
		padding: rem(20);
		background-color: var(--bgc-second);
		border-radius: rem(2);
	}
	// .result__title
	&__title {
		display: flex;
		align-items: center;
		gap: 0 rem(10);
		font-size: rem(28);
	}
	// .result__icon
	&__icon {
		width: rem(32);
		height: rem(32);
	}
	// .result__text
	&__text {
		font-size: rem(24);
	}
	&__link {
		font-size: rem(24);
		color: var(--fc-accent);
		text-decoration: underline;
	}
}

.button {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: rem(20);
	gap: 0 rem(20);
	border: 0;
	border-radius: rem(2);
	background-color: var(--bgc-accent);
	font-family: inherit;
	font-size: rem(20);
	font-weight: 600;
	color: var(--fc-primary);

	&_unactive {
		background-color: var(--bgc-second);
		cursor: unset;
	}

	&:not(.button_unactive):hover {
		& .button__icon {
			&_prev {
				transform: rotate(180deg) translate(rem(20), 0);
			}
			&_next {
				transform:translate(rem(20), 0);
			}
		}
	}


	// .button__icon
	&__icon {
		width: rem(10);
		height: rem(16);
		fill: #FFFFFF;
		transition: transform 0.6s ease 0s;
		// .button__icon_prev
		&_prev {
			transform: rotate(180deg);
		}
	}
}

.hide {
	display: none;
}
