.header {
	position: absolute;
	width: 100%;
	height: rem(95);
	padding: rem(15) 0;
	left: 0;
	top: 0;
	z-index: 15;
	// .header__container
	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	// .header__nav
	&__nav {
		@include media('max', 'width', 991.98) {
			position: fixed;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			transform: translate(0, -100%);
			background-color: var(--bgc-primary);
			transition: transform 0.4s ease-in-out 0s;
			&_active {
				transform: translate(0, 0);
			}
		}
	}
	// .header__nav-list
	&__nav-list {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include media('min', 'width', 992) {
			flex-direction: row;
			gap: 0 rem(50);
		}
		@include media('max', 'width', 991.98) {
			padding: rem(50) 0;
		}
		// .header__nav-list__item
		&__item {
			@include media('min', 'width', 992) {
				position: relative;
			}
		}
		// .header__nav-list__link
		&__link {
			display: inline-block;
			font-size: rem(20);
			font-weight: 600;
			color: var(--fc-primary);
			transition: color 0.2s ease-in-out 0s;
			@include media('max', 'width', 991.98) {
				padding: rem(10);
			}
			&:hover {
				color: var(--fc-accent);
				& .header__nav-icon {
					fill: #6ECCAF;
				}
			}
			// .header__nav-list__link_sub
			&_sub {
				display: flex;
				align-items: center;
				gap: 0 rem(15);
			}
			&_active {
				color: var(--fc-accent);
			}
		}
	}
	// .header__nav-icon
	&__nav-icon {
		width: rem(10);
		height: rem(16);
		transform: rotate(90deg);
		fill: #FFFFFF;
		transition: all 0.2s ease-in-out 0s;
		&_active {
			fill: #6ECCAF;
			transform: rotate(-90deg);
		}
	}
	// .header__nav-sub
	&__nav-sub {
		padding: rem(15) rem(20);
		gap: rem(15) 0;
		@include media('min', 'width', 992) {
			display: grid;
			position: absolute;
			width: 100%;
			left: 0;
			top: calc(100% + rem(20));
			border-radius: rem(2);
			transform: translate(0, rem(40));
			visibility: hidden;
			opacity: 0;
			transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, visibility 0.3s ease 0s;
			background-color: var(--bgc-accent);
		}
		@include media('max', 'width', 991.98) {
			display: none;
			border-right: rem(3) solid var(--bgc-accent);
			text-align: end;
		}
		// .header__nav-sub_active
		&_active {
			@include media('min', 'width', 992) {
				transform: translate(0, 0);
				opacity: 1;
				visibility: visible;
			}
			@include media('max', 'width', 991.98) {
				display: grid;
			}
		}
		// .header__nav-sub__link
		&__link {
			font-size: rem(18);
			font-weight: rem(600);
			color: var(--fc-primary);
			&:hover {
				text-decoration: underline;
			}
		}
	}
}

.logo {
	display: flex;
	align-items: center;
	max-width: rem(300);
	gap: 0 rem(10);
	@include media('min', 'width', 992) {
		gap: 0 rem(20);
	}
	// .logo__icon
	&__icon {
		flex-shrink: 0;
		width: rem(40);
		@include media('min', 'width', 992) {
			width: rem(50);
		}
	}
	// .logo__text
	&__text {
		font-size: rem(20);
		font-weight: 700;
		color: var(--fc-accent);
		@include media('min', 'width', 992) {
			font-size: rem(24);
		}
	}
}

.burger {
	display: none;
	@include media('max', 'width', 991.98) {
		position: relative;
		display: grid;
		width: rem(30);
		gap: rem(7) 0;
		background: transparent;
		border: 0;
		// .burger_active
		&_active {
			// .burger__line
			& .burger__line {
				&:nth-child(1) {
					transform: translate(0, rem(10)) rotate(45deg);
				}
				&:nth-child(3) {
					transform: translate(0, rem(-10)) rotate(-45deg);
				}
				&:nth-child(2) {
					opacity: 0;
				}
			}
		}
	}
	// .burger__line
	&__line {
		@include media('max', 'width', 991.98) {
			height: rem(3);
			background-color: var(--bgc-third);
			border-radius: rem(2);
			transition: transform 0.4s  ease-in-out 0s, opacity 0.4s  ease-in-out 0s;
		}
	}
}
